<template>
    <ion-page>
        <ion-content>
            <ion-header>
                <ion-toolbar>
                    <ion-buttons slot="start">
                        <ion-button class="iconbutton" @click="GoBack">
                            <ion-icon :icon="arrowBackOutline" size="large"></ion-icon>
                        </ion-button>
                    </ion-buttons>
                    <ion-title>Add friend</ion-title>
                </ion-toolbar>
            </ion-header>
                <div>
                    <form action="/action_page.php">
                        <input type="text" id="searchinput" name="searchinput" placeholder="Email/username">
                    </form>
                </div>
                <ion-button @click="SearchForFriend">Search for friend</ion-button>
                <ConfirmAddFriendAlert v-if="friendSearchResult.length !=0" v-bind:friend="friendSearchResult[0]"></ConfirmAddFriendAlert>


        </ion-content>
    </ion-page>
</template>

<script>
import { IonPage, IonContent, IonIcon, IonButton, IonButtons, IonTitle} from '@ionic/vue';
import {arrowBackOutline} from 'ionicons/icons';
import ConfirmAddFriendAlert from './ConfirmAddFriendAlert.vue';

export default {
    name: 'addfriend',
    data: function() {
      return {
        arrowBackOutline,
      };  
    },
    components: {
        IonPage,
        IonContent,
        IonIcon,
        IonButton,
        IonButtons,
        IonTitle,
        ConfirmAddFriendAlert
    },
    computed: {
        friendSearchResult() {
          return this.$store.getters.getFriendSearchResult;
        },
    },
    methods: {
        GoBack() {
            this.$router.push('/friendslist');
        },
        SearchForFriend() {
            this.$store.dispatch('setFriendSearchResult', document.getElementById('searchinput').value);
        }
    },
}
</script>
<style scoped>
ion-icon {
    color: white;
}

ion-button {
  --background: #262A34;
  --padding-top: 1.5vh;
  --padding-bottom: 1.5vh;
  --border-radius: 6px;
  margin-top: 1.5vh;
  margin-left: 5vw;
  margin-right: 5vw;
}

ion-buttons {
    --background: #181A20;
}

.iconbutton {
    --background: #181A20;
}

h1 {
    color:white;
    text-align: center;
    margin-top: 5vh;
    margin-bottom: 4vh;
}

ion-header {
    position: relative;
    height: 10vh;
}

ion-toolbar {
    --background: #181A20;
    margin: 0;
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

ion-title {
       --background: #141A33;
       color: white;
}

input[type=text], select {
  width: 80%;
  border-radius: 4px;
  box-sizing: border-box;
  height: 4vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #262A34;
  background-color: #262A34;
  color: white;
}


</style>